    <p>
      Liza Jane McAlister is a fourth-generation rancher out in the Wallowa
      River Valley near Enterprise, Oregon, on an exceptionally beautiful piece
      of land called the 6 Ranch. She's a spirited, original woman with roots in
      the ground and ideas as free as the clouds. She's also fiercely loyal to
      her region, her neighbors and their long experience of cattle ranching,
      forestry and land management. Her son and daughter are part of the story
      as well: James is a fishing and hunting guide on the family land, and
      Adele runs a neighboring cattle ranch with her husband.
    </p>

    <p>
      In the video above, Liza Jane recounts her experience with two large
      stream restoration projects on the ranch. These projects have brought the
      meanders of the Wallowa River out of a straightened and radically
      simplified channel that was bulldozed along the bluff sometime shortly
      after WWII. That work was done in order to control flooding and protect
      the railway; but it inadvertently ruined fish, animal, bird and insect
      habitat in the process.
    </p>

    <p>
      The new river seeks to be a fusion of art and nature, restoring a complex
      stream bed rich in habitat of many kinds, while still stabilizing the
      river's course and preventing it from damaging both the ranch and public
      infrastructures. However this project too required massive earth-moving
      equipment, as well as a plethora of hydrologists, biologists, ecologists,
      foresters, wildlife specialists and many others, all coordinated by the
      Grande Ronde Model Watershed. That organization in its turn is part of the
      Oregon Watershed Enhancement Board, OWEB, which funds local watershed
      councils and restoration initiatives across the state. It is amazing to
      see that the watershed council, one of the key proposals of 1970s-era
      bioregionalism, has now been instituted as part of state
      government&mdash;with all the ambivalent results that can bring.
    </p>

    <p>
      Let's say it out loud, just as Liza Jane does. The common wisdom east of
      the Cascades is that if you let the government on your property, they'll
      take over your land. A woman needs courage to buck that deep-rooted
      belief, and also lots of stubborn negotiation and caeful
      strategy&mdash;because if you look into it, you will find there is some
      truth to the common wisdom.
    </p>

    <p>
      Liza Jane McAlister thinks that the restoration of the Wallowa River was
      well worth the effort, and she has strong ideas that can apply to both
      sides of the notorious urban-rural divide. In particular, she does not
      accept the principle that grazing inevitably destroys riverine
      environments, and she proposes using cattle as a tool of land management,
      in a practice also known as "regenerative grazing." Underlying this
      message is another, deeper one: the land will not be restored without the
      positive contributions of the people who actually live there. As the video
      makes clear, the 6 Ranch produces food for thought, along with very tasty
      things to eat.
    </p>

